.header {
  display: flex;
  justify-content: space-between;

  height: 70px;
  margin: 10px;
  padding: 7px;
  border: 1px solid rgb(181, 177, 177);
  border-radius: 50px;
}
.logo {
  width: 100px;
}
.logo > img {
  margin-left: 20px;
  width: 100%;
}
.list > ul {
  display: flex;
}
.list > ul > li {
  list-style: none;
  padding: 10px;
}
.header > button {
  width: 70px;
  border-radius: 50%;
  background-color: tomato;
  color: white;
  font-size: 17px;
  border: none;
}
